.header{
    border-bottom: 1px solid #e0e0e0;
}
.header_content_container{
    width: 70%;
    max-width: 1380px;
    margin: 0 auto;
    display: -ms-flexbox;
    display: flex;

}
.header_content_left {
    -ms-flex:1 1;
        flex:1 1;

}
.header_content_right{
    -ms-flex:1 1;
        flex:1 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}
.header_content_right input{
    width:50%;
    padding: 10px 20px;
    font-size: 20px;
    margin-right: 10px;
    color:#4a4a4a;
    
}
.header_content_left h1{
    margin:20px auto; 
    color:#4a4a4a;
    text-shadow: 1px 1px 3px #333; 
}
.header_content_right i{
    font-size: 20px;
    color:#4a4a4a;
}
@media screen and (max-width:800px){
    .header_content_container{
        -ms-flex-direction: column;
            flex-direction: column;
        
    }
    .header_content_left{
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: center;
            justify-content: center;
        -ms-flex-align: center;
            align-items: center;
    }
    .header_content_right{
            height: 80px;
    }
}


